blockquote {
  margin: 30px 0;
  font-size: 1.4375rem;
  line-height: 1.5em;
  color: #505050;
  text-align: center;
  font-style: italic;
}

table {
  width: 100%;
  margin: 30px 0;
  border-collapse: collapse;
}

table,
td,
th {
  border: 1px solid #dadada;
  text-align: left;
}

th {
  font-weight: 800;
}

th,
td {
  padding: 15px;
}

figure {
  margin: 30px 0;
}

figure img {
  margin: 0;
}

figcaption {
  margin-top: 10px;
  color: #6a6969;
  font-size: 16px;
}

pre {
  display: block;
  margin: 0 0 40px 0;
  padding: 20px;
  background-color: #f9f9f9;
  border: 1px solid #dadada;
  overflow: auto;
  border-radius: 3px;
}

pre code {
  position: static;
  padding: 0;
  border: none;
  line-height: 1.5em;
}

code {
  position: relative;
  top: -0.2em;
  padding: 3px;
  font-family: Courier New, Courier, monospace;
  font-size: 0.8333333333rem;
  color: #282727;
  line-height: 1em;
  pointer-events: none;
  border: 1px solid #dadada;
  border-radius: 3px;
}

em,
i,
.italic {
  font-style: italic;
}

strong,
b,
.bold {
  font-weight: 800;
}

img {
  display: block;
  max-width: 100%;
  margin: 30px 0;
}
